import React from 'react';
import css from './profile.module.css';
import { NavLink, useLocation, Outlet } from "react-router-dom";
import { logOutUser } from '../../services/actions/userAction'
import {useDispatch} from "../../services/store";

type TLinks = {
    to: string;
    text: string;
    description: string;
}

const ORDERS_SERVER_URL = "ws://localhost:3000";

export const ProfilePage = (): React.JSX.Element => {
    const dispatch = useDispatch();
    const location = useLocation();

    const links: Array<TLinks> = [
        {
            to: "",
            text: "",
            description: "        "
        },
        {
            to: "orders",
            text: " ",
            description: "        "
        }
    ];
    const onLogout = () => {
        // @ts-ignore
        dispatch(logOutUser());
    }
    return (
        <div className={css.wrapper}>
            <nav className={css.links}>
                {links.map((link, index) => (
                    <NavLink
                        key={index}
                        end
                        to={link.to}
                        className={({isActive}) => isActive ? css.linkActive : css.linkNoActive}
                    >
                        {link.text}
                    </NavLink>
                ))}
                <button className={css.button} onClick={onLogout}>
                    
                </button>
                {location.pathname === "/profile" && (
                    <p className={css.text}>
                        {links[0].description}
                    </p>
                )}
                {location.pathname === "/profile/orders" && (
                    <div>
                        <h3 className={css.text}>
                            {links[1].description}
                        </h3>
                        {/* <p>
                             : <span>{status}</span>
                        </p>
                        <div>
                            <button
                                disabled={!isDisconnected}
                                onClick={connect}
                            >
                                
                            </button>
                            <button
                                disabled={isDisconnected}
                                onClick={disconnect}
                            >
                                
                            </button>
                        </div> */}
                        {/* <OrderTable table={table}/> */}
                    </div>
                )}
            </nav>
            <main>
                <Outlet />
            </main>
        </div>
    );
}
